import { Component, OnInit, ViewChild } from "@angular/core";
import { NguCarousel } from "@ngu/carousel";
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { filter, map, mergeMap } from "rxjs/operators";
import { PageService } from "../api/page.service";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { environment } from "../../environments/environment";
import { RoomComponent } from "../room/room.component";
import { QuoteFormComponent } from "../quote-form/quote-form.component";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";
import {
  NgxGalleryOptions,
  NgxGalleryImage,
  NgxGalleryAnimation
} from "ngx-gallery";
import "hammerjs";
import * as cheerio from "cheerio";
import AOS from 'aos';


@Component({
  selector: "app-hotel",
  templateUrl: "./hotel.component.html",
  styleUrls: ["./hotel.component.css"]
})
export class HotelComponent implements OnInit {
  @ViewChild(RoomComponent)
  roomModal: RoomComponent;
  @ViewChild(QuoteFormComponent)
  quoteModal: QuoteFormComponent;

  galleryOptions: NgxGalleryOptions[];
  hotelGalleryOptions: NgxGalleryOptions[];
  //galleryImages: any = [];
  galleryImages: NgxGalleryImage[];
  hotelGalleryImages: NgxGalleryImage[];

  @BlockUI()
  blockUI: NgBlockUI;
  // bannerImg = "assets/images/banner.jpg";
  bannerImages = [
    "assets/images/banners/maldives-banner-1.jpg",
    "assets/images/banners/maldives-banner-2.jpg",
    "assets/images/banners/maldives-banner-3.jpg",
    "assets/images/banners/maldives-banner-4.jpg"
  ];
  bannerImg = this.bannerImages[
    Math.floor(Math.random() * this.bannerImages.length)
  ];
  logo = "assets/images/hotel-logo-1.jpg";
  title = "Hotel";
  hotelId = 0;
  contentText = "";
  contentHeadings = [];
  contentDescriptions = [];
  address = "";
  starRating = "0";
  distance = "0";
  no_of_rooms = "0";
  travel = "";
  recommended_for = "";
  destination = "";
  price = "";
  rooms: any = [];
  services: any = [];
  galleries: any = [];
  currentUrl = "";
  Object = Object;
  public destinationCarousel;

  apiUrl = "";
  slug = "";

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private page: PageService
  ) {}

  ngOnInit() {
    AOS.init({
      once:true, disable: 'mobile'
    });
     // start the page from the top
     this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
  });
    this.apiUrl = environment.site_url;
    this.blockUI.start("");
    this.currentUrl = this.router.url;

    this.galleryOptions = [
      {
        width: "100%",
        // height: "400px",
        thumbnailsColumns: 4,
        imageAnimation: NgxGalleryAnimation.Slide,
        preview: false,
        imageInfinityMove: true,
        previewZoom: true,
        imageArrowsAutoHide: true,
        thumbnailsArrowsAutoHide: true
 


      }
      // max-width 800
      // {
      //   breakpoint: 800,
      //   width: "100%",
      //   height: "600px",
      //   imagePercent: 80,
      //   thumbnailsPercent: 20,
      //   thumbnailsMargin: 20,
      //   thumbnailMargin: 20
      // },
      // // max-width 400
      // {
      //   breakpoint: 400,
      //   preview: false
      // }
    ];
    this.hotelGalleryOptions = [
      {
        width: "100%",
        height: "600px",
        thumbnailsColumns: 6,
        imageAnimation: NgxGalleryAnimation.Slide,
        //previewForceFullscreen: true,
        previewCloseOnClick: true,
        //preview: false,
        imageInfinityMove: true,
        imageArrowsAutoHide: true,
        thumbnailsArrowsAutoHide: true


      }
      // max-width 800
      // {
      //   breakpoint: 1200,
      //   thumbnailsPercent: 20,
      //   thumbnailsColumns: 5
      // },
      // {
      //   breakpoint: 991,
      //   width: "100%",
      //   height: "600px",
      //   //imagePercent: 80,
      //   thumbnailsPercent: 20,
      //   //thumbnailsMargin: 20,
      //   //thumbnailMargin: 20,
      //   thumbnailsColumns: 4
      // },
      // max-width 400
      // {
      //   breakpoint: 400,
      //   preview: false
      // }
    ];

    this.destinationCarousel = {
      grid: { xs: 1, sm: 1, md: 1, lg: 1, all: 0 },
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: false
      },
      load: 1,
      touch: true,
      loop: true
    };

    this.activatedRoute.params.subscribe(params => {
      this.slug = params["slug"];
      if (typeof this.slug == "undefined") {
        this.slug = this.currentUrl.substring(
          this.currentUrl.lastIndexOf("/") + 1,
          this.currentUrl.length
        );
      }
    });

    console.log("Current Slug : ", this.slug);
    console.log("SAAD");

    if (!this.slug) return false;

    this.page.getHotel(this.slug).then(
      res => {
        if (res["success"]) {
          const pageData = res["response"];
          this.rooms = pageData["rooms"];
          this.services = pageData["services"];
          this.galleries = pageData["galleries"];
          this.title = pageData["title"];
          this.hotelId = pageData["id"];
          if (pageData["banner"]) {
            this.bannerImg = this.apiUrl + pageData["banner"];
          }
          this.logo = pageData["logo"];
          this.contentText = pageData["content"];
          this.contentHeadings = [];
          var Cheerio = cheerio.load(this.contentText);
          var contentHeadings = [];
          Cheerio("h3.title").each(function() {
            contentHeadings.push(Cheerio(this).html());
          });
          this.contentHeadings = contentHeadings;
          var contentDescriptions = [];
          Cheerio("p.detail").each(function() {
            contentDescriptions.push(Cheerio(this).html());
          });
          this.contentDescriptions = contentDescriptions;
          // console.log(this.contentHeadings);
          // console.log(this.contentDescriptions);

          this.address = pageData["address"];
          this.starRating = pageData["starRating"];
          this.destination = pageData["destinationTitle"];
          this.price = pageData["price"];
          this.distance = pageData["distance"];
          this.no_of_rooms = pageData["no_of_rooms"];
          this.travel = pageData["travel"];
          this.recommended_for = pageData["recommended_for"];

          var roomGallery = [];
          var hotelGalleryImages = [];
          if (this.rooms.length > 0) {
            this.rooms.forEach(room => {
              if (room.galleries && room.galleries.length > 0) {
                // Clear Array
                var gallery = [];
                room.galleries.forEach(room_gallery => {
                  gallery.push({
                    small: this.apiUrl + room_gallery["roomGalleryImg"],
                    medium: this.apiUrl + room_gallery["roomGalleryImg"],
                    big: this.apiUrl + room_gallery["roomGalleryImg"]
                  });
                  // this.galleryImages.push({
                  //   small: this.apiUrl + room_gallery["roomGalleryImg"],
                  //   medium: this.apiUrl + room_gallery["roomGalleryImg"],
                  //   big: this.apiUrl + room_gallery["roomGalleryImg"]
                  // });
                });
                roomGallery[room.id] = gallery;
                hotelGalleryImages = hotelGalleryImages.concat(gallery);
              }
            });
          }
          // console.log(roomGallery);
          this.galleryImages = roomGallery;
          this.hotelGalleryImages = hotelGalleryImages;
          console.log("Gallery Images: ");
          console.log(this.galleryImages);

          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );
  }

  change_thumbnail(room, img) {
    room.thumbnail = img;
  }

  showQuoteForm(hotelId, title, rooms, roomId, $event) {
    $event.preventDefault();
    this.quoteModal.show(hotelId, title, rooms, roomId);
  }
}

