import { Component, OnInit, Input } from "@angular/core";
import { NguCarousel } from "@ngu/carousel";
import { PageService } from "../api/page.service";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { environment } from "../../environments/environment";
import { IMasonryGalleryImage } from 'ngx-masonry-gallery';
import { trigger, state, style, animate, transition } from '@angular/animations';
import {BrowserModule, Title, Meta} from "@angular/platform-browser";
import { Router, NavigationEnd } from '@angular/router';
import AOS from 'aos';

@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"],
  animations: [
    trigger('fadeInOut', [
      state('void', style({
        opacity: 0
      })),
      transition('void <=> *', animate(1000)),
    ])
  ]
})
export class HomeComponent implements OnInit {

  ///
  private urls: string[] = [
    './assets/gal/about-img1.jpg',
    './assets/gal/dest_ams.png',
    './assets/gal/about-img2.jpg',
    './assets/gal/dest_bal.png'
];

public get images(): IMasonryGalleryImage[] {
    return this.urls.map(m => <IMasonryGalleryImage>{
        imageUrl: m
});
}
  @BlockUI()
  blockUI: NgBlockUI;
  public bannerCarousel;
  public destinationCarousel;
  public testimonialCarousel;
  public resortCarousel;

  bannerHeading = "Spend Quality Holidays With Us";
  bannerDescription =
    "Were helping people to travel smart within their budget";
  bannerImg = "assets/images/banners/home-banner-1.jpg";
  bannerPageLink = "#";
  contentText = "";
  destinations = [];
  resorts = [];
  testimonials = [];
  apiUrl = "";
  bannerImages = [
    "assets/images/banners/home-banner-1.jpg",
    "assets/images/banners/home-banner-2.jpg"
  ];

  constructor(
    private page: PageService,
    private title: Title,
    private meta : Meta,
    private router: Router
    ){ 
    this.title.setTitle('Rawnaq Tourism - Plan Your Best Holidays Trip, Family and Honeymoon Tour Packages');
    this.meta.updateTag({ name: 'description', content: 'Make your Holiday Special with Rawnaq Tourism Book our Luxury travel Tour, Honeymoon Tour Package, Family and Couple Tour Package at best destination in the world.' });
    }

  async ngOnInit() {
    AOS.init({
      once:true, disable: 'mobile'
    });
    this.apiUrl = environment.site_url;
    this.bannerCarousel = {
      grid: { xs: 1, sm: 1, md: 1, lg: 1, all: 0 },
      slide: 4,
      speed: 2000,
      interval: 8000,
      point: {
        visible: false
      },
      load: 2,
      touch: false,
      loop: true
    };
    this.testimonialCarousel = {
      grid: { xs: 1, sm: 1, md: 1, lg: 1, all: 0 },
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: false
      },
      load: 2,
      touch: true,
      loop: true
    };
    this.destinationCarousel = {
      grid: { xs: 1, sm: 1, md: 3, lg: 3, all: 0 },
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: false
      },
      load: 2,
      touch: true,
      loop: true
    };
    this.resortCarousel = {
      grid: { xs: 1, sm: 1, md: 3, lg: 3, all: 0 },
      slide: 1,
      speed: 400,
      interval: 4000,
      point: {
        visible: false
      },
      load: 2,
      touch: true,
      loop: true
    };

    this.blockUI.start(""); // Start blocking
    await this.page.getHomePage().then(
      res => {
        console.log("Home Page: ", res);
        if (res["success"]) {
          const page = res["response"];
          if (page["banner_heading"] !== "")
            this.bannerHeading = page["banner_heading"];
          if (page["banner_description"] !== "")
            this.bannerDescription = page["banner_description"];
          if (page["banner_img"] !== "")
            this.bannerImg = environment.site_url + page["banner_img"];
          if (page["banner_page_link"] !== "")
            this.bannerPageLink = page["banner_page_link"];
          this.contentText = page["content_text"];
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );

    this.blockUI.start(""); // Start blocking
    await this.page.getAllDestination(6, 1).then(
      res => {
        console.log("All Destination: ", res);
        if (res["success"]) {
          this.destinations = res["response"];
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );

    this.blockUI.start(""); // Start blocking
    await this.page.getTopResorts(10).then(
      res => {
        console.log("Top Resorts: ", res);
        if (res["success"]) {
          this.resorts = res["response"];
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );

    this.blockUI.start(""); // Start blocking
    await this.page.getTestimonials(5).then(
      res => {
        console.log("All Testimonials: ", res);
        if (res["success"]) {
          this.testimonials = res["response"];
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );

       // start the page from the top
       this.router.events.subscribe((evt) => {
        if (!(evt instanceof NavigationEnd)) {
            return;
        }
        window.scrollTo(0, 0)
    });

    
  }

}
