import { BrowserModule, Title, Meta } from "@angular/platform-browser";
import { NgModule, APP_INITIALIZER } from "@angular/core";
import { HttpClientModule } from "@angular/common/http";
import { Routes, RouterModule } from "@angular/router";
import { BlockUIModule } from "ng-block-ui";
import { NguCarouselModule } from "@ngu/carousel";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { InfiniteScrollModule } from "ngx-infinite-scroll";
import { NgbModule } from "@ng-bootstrap/ng-bootstrap";

import { AppComponent } from "./app.component";
import { HomeComponent } from "./home/home.component";
import { AppRoutingModule } from "./route/app-routing.module";
import { ApiModule } from "./api/api.module";
import { PipeModule } from "./pipe/pipe.module";
import { InjectorModule } from "./injector/injector.module";

import { RoutesService } from "./api/routes.service";
import { LoginComponent } from "./login/login.component";
import { DestinationsComponent } from "./destinations/destinations.component";
import { PageComponent } from "./page/page.component";
import { ExploreComponent } from "./explore/explore.component";
import { HotelComponent } from "./hotel/hotel.component";
import { RoomComponent } from "./room/room.component";
import { QuoteFormComponent } from "./quote-form/quote-form.component";
import { ContactFormComponent } from "./contact-form/contact-form.component";
import { HowWeWorkComponent } from "./how-we-work/how-we-work.component";
import { EnquiryFormComponent } from "./enquiry-form/enquiry-form.component";
import { NgxGalleryModule } from "ngx-gallery";
import { AboutUsComponent } from './about-us/about-us.component';
import { BankDetailsComponent } from './bank-details/bank-details.component';
import { OnlineBookingComponent } from './online-booking/online-booking.component';
import { MasonryGalleryModule } from 'ngx-masonry-gallery';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { MetaModule } from '@ngx-meta/core';
import { SearchComponent } from './search/search.component';



export function initRoutes(routesService: RoutesService) {
  return () => routesService.createNavigation();
}

@NgModule({
  declarations: [
    AppComponent,
    HomeComponent,
    LoginComponent,
    DestinationsComponent,
    PageComponent,
    ExploreComponent,
    HotelComponent,
    RoomComponent,
    QuoteFormComponent,
    ContactFormComponent,
    HowWeWorkComponent,
    EnquiryFormComponent,
    AboutUsComponent,
    BankDetailsComponent,
    OnlineBookingComponent,
    SearchComponent
    
  ],
  imports: [
    BrowserModule,
    //AppRoutingModule,
    HttpClientModule,
    BlockUIModule.forRoot(),
    NgbModule.forRoot(),
    NguCarouselModule,
    FormsModule,
    ReactiveFormsModule,
    InfiniteScrollModule,
    //RouterModule,
    RouterModule.forRoot([]),
    ApiModule,
    InjectorModule,
    PipeModule,
    NgxGalleryModule,
    MasonryGalleryModule,
    BrowserAnimationsModule,
    //RouterModule.forRoot(routes),
    MetaModule.forRoot()

  ],
  entryComponents: [
    HomeComponent,
    LoginComponent,
    PageComponent,
    DestinationsComponent,
    ExploreComponent,
    HotelComponent,
    ContactFormComponent,
    HowWeWorkComponent,
    AboutUsComponent,
    BankDetailsComponent,
    OnlineBookingComponent,
    SearchComponent
  ],
  providers: [
    Title,
    Meta,
    {
      provide: APP_INITIALIZER,
      useFactory: initRoutes,
      deps: [RoutesService],
      multi: true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule {

}
