import { Component, OnInit, ViewChild, Input, Output } from '@angular/core';
import { NgbModal, ModalDismissReasons } from '@ng-bootstrap/ng-bootstrap';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-room',
  templateUrl: './room.component.html',
  styleUrls: ['./room.component.css']
})
export class RoomComponent implements OnInit {
    @ViewChild('roomContent') content: any;
    //@Input() 
    room :any;
    apiUrl = "";
    
    modalRef: any;
    currentImage = "assets/images/sunset.jpg"
    Object = Object;
    
    constructor(
        private modalService: NgbModal
    ) { }

    show(room) {
        this.apiUrl = environment.site_url;
        this.room = room;
        if(typeof room['galleries'] !== 'undefined' && room['galleries'].length>0){
            this.currentImage = room['galleries'][0]['roomGalleryImg'];
        }else{
            room['galleries'] = [];
            this.currentImage = room['thumbnail'];
        }
        this.modalRef = this.modalService.open(this.content, { windowClass: 'xlModal', size: 'lg', backdrop: 'static' });
    }
    
    close(event: any) {
        event.stopPropagation();
        this.modalRef.close();
      }
    
    showImg(img){
        this.currentImage = img;
    }
    ngOnInit() {
        this.apiUrl = environment.site_url;
    }

}
