import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { filter, map, mergeMap } from "rxjs/operators";
import { PageService } from "../api/page.service";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { environment } from "../../environments/environment";

@Component({
  selector: "app-page",
  templateUrl: "./page.component.html",
  styleUrls: ["./page.component.css"]
})
export class PageComponent implements OnInit {
  @BlockUI()
  blockUI: NgBlockUI;
  bannerImg = "assets/images/about-banner.jpg";
  title = "";
  contentText = "";
  apiUrl = "";

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private page: PageService
  ) {}

  ngOnInit() {
    this.apiUrl = environment.site_url;
    const currentUrl = this.router.url;

    this.page.getPageContent(currentUrl.replace("/", "")).then(
      res => {
        console.log("Page " + currentUrl + ": ", res);
        if (res["success"]) {
          const pageData = res["response"];
          this.title = pageData["title"];
          this.contentText = pageData["content"];
          if (pageData["banner_img"] !== "") {
            this.bannerImg = environment.site_url + pageData["banner_img"];
          }
          if (pageData["title"] == "About") {
            setTimeout(() => {
              // step 1: find all the child elements with the selected class name
              var childrens = document.getElementsByClassName("matchheight");
              // console.log(console.log(childrens));
              if (!childrens) return;

              // step 2a: get all the child elements heights
              const itemHeights = Array.from(childrens).map(
                x => x.getBoundingClientRect().height
              );

              // step 2b: find out the tallest
              const maxHeight = itemHeights.reduce((prev, curr) => {
                return curr > prev ? curr : prev;
              }, 0);

              // step 3: update all the child elements to the tallest height
              Array.from(childrens).forEach(
                (x: HTMLElement) => (x.style.height = `${maxHeight}px`)
              );
            }, 200);
          }
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );
  }
}
