import { Component, OnInit } from "@angular/core";
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { filter, map, mergeMap } from "rxjs/operators";
import { PageService } from "../api/page.service";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { environment } from "../../environments/environment";
import {
  trigger,
  state,
  style,
  animate,
  transition
} from "@angular/animations";
import { Title } from "@angular/platform-browser";
import AOS from "aos";

@Component({
  selector: "app-destinations",
  templateUrl: "./destinations.component.html",
  styleUrls: ["./destinations.component.css"],
  animations: [
    trigger("fadeInOut", [
      state(
        "void",
        style({
          opacity: 0
        })
      ),
      transition("void <=> *", animate(1000))
    ])
  ]
})
export class DestinationsComponent implements OnInit {
  @BlockUI()
  blockUI: NgBlockUI;
  bannerImg = "assets/images/about-banner.jpg";
  title = "Destination";
  apiUrl = "";
  scrollDistance = 1;
  scrollUpDistance = 2;
  throttle = 0;
  perPage = 12;
  destinations = [];
  displayDestinations = [];
  contentText = "";
  totalPages = [];
  currentPage = 0;
  search_destination = "";

  constructor(
    private activatedRoute: ActivatedRoute,
    private page: PageService,
    private title_: Title,
    private router: Router
  ) {
    this.title_.setTitle("RAWNAQ Tourism | Destinations");
  }

  async ngOnInit() {
    AOS.init({
      once: true,
      disable: "mobile"
    });
    this.apiUrl = environment.site_url;
    this.blockUI.start("");

    // start the page from the top
    const currentUrl = this.router.url;
    this.router.events.subscribe(evt => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0);
    });

    await this.page.getPageContent(currentUrl.replace("/", "")).then(
      res => {
        console.log("Page " + currentUrl + ": ", res);
        if (res["success"]) {
          const pageData = res["response"];
          this.title = pageData["title"];
          this.contentText = pageData["content"];
          if (pageData["banner_img"] !== "")
            this.bannerImg = environment.site_url + pageData["banner_img"];
          this.blockUI.stop();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );

    await this.loadDestinationsPage(12, 0, 0);
  }

  loadDestinationsPage(limit = 12, show_on_home = 0, page = 0) {
    var search_string = "";
    if (this.search_destination.trim() !== "") {
      search_string = this.search_destination.trim();
    }
    this.page.getAllDestination(limit, show_on_home, page, search_string).then(
      res => {
        console.log("All Destination: ", res);
        if (res["success"]) {
          this.destinations = res["response"];
          this.displayDestinations = [];
          this.currentPage = res["current_page"];
          // console.log(res);
          this.totalPages = Array(res["total_pages"])
            .fill(0)
            .map((x, i) => i);
          const BreakException = {};
          try {
            this.destinations.forEach((s, i) => {
              if (this.perPage < i + 1) {
                throw BreakException;
              }
              this.displayDestinations.push(this.destinations[i]);
            });
          } catch (e) {
            if (e !== BreakException) {
              throw e;
            }
          }
          this.blockUI.stop();
        } else {
          this.displayDestinations = [];
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );
  }

  async search() {
    await this.loadDestinationsPage(12, 0, 0);
  }

  loadMoreDestination() {
    if (this.destinations && this.destinations.length > 0) {
      this.destinations.forEach((s, i) => {
        const offSet = this.displayDestinations.length;
        const BreakException = {};
        try {
          if (
            this.displayDestinations.length - 1 < i &&
            this.perPage + offSet > i
          ) {
            this.displayDestinations.push(this.destinations[i]);
          } else if (
            this.perPage + offSet <= i + 1 ||
            (this.perPage + offSet > this.destinations.length &&
              i + this.perPage > this.destinations.length)
          ) {
            throw BreakException;
          }
        } catch (e) {
          if (e !== BreakException) {
            throw e;
          }
        }
      });
    }
  }
}
