import { Component, OnInit } from '@angular/core';
import { trigger, state, style, animate, transition } from '@angular/animations';
import {Title} from "@angular/platform-browser";
import { Router, NavigationEnd } from '@angular/router';
import AOS from 'aos';


@Component({
  selector: 'app-online-booking',
  templateUrl: './online-booking.component.html',
  styleUrls: ['./online-booking.component.scss'],
  animations: [
    trigger('fadeInOut', [
      state('void', style({
        opacity: 0
      })),
      transition('void <=> *', animate(1000)),
    ])
  ]
})
export class OnlineBookingComponent implements OnInit {

  constructor(private title: Title, private router: Router) { 
    this.title.setTitle('RAWNAQ Tourism | Online Booking');
  }


  ngOnInit() {
    AOS.init({
      once:true, disable: 'mobile'
    });
     // start the page from the top
     this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
  });
  
  }

}
