import { Component, OnInit } from "@angular/core";
import { environment } from "../../environments/environment";
import { FormsModule } from "@angular/forms";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { PageService } from "../api/page.service";
import { trigger, state, style, animate, transition } from '@angular/animations';
import {Title} from "@angular/platform-browser";
import { Router, NavigationEnd } from '@angular/router';
import AOS from 'aos';


@Component({
  selector: "app-contact-form",
  templateUrl: "./contact-form.component.html",
  styleUrls: ["./contact-form.component.css"],
  animations: [
    trigger('fadeInOut', [
      state('void', style({
        opacity: 0
      })),
      transition('void <=> *', animate(1000)),
    ])
  ]
})
export class ContactFormComponent implements OnInit {
  @BlockUI()
  blockUI: NgBlockUI;
  modelAlertContent = "";
  f = {
    clientName: "",
    clientContact: "",
    clientEmail: "",
    clientSubject: "",
    clientContent: ""
  };
  bannerImg = "assets/images/about-banner.jpg";
  title = "Contact us";
  contentText = "";
  apiUrl = "";

  constructor(private page: PageService, private title_: Title, private router: Router) { 
    this.title_.setTitle('RAWNAQ Tourism | Contact us');
  }

  ngOnInit() {
    AOS.init({
      once:true, disable: 'mobile'
    });
    this.apiUrl = environment.site_url;
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
          return;
      }
      window.scrollTo(0, 0)
  });
  }

  

  onSend() {
    if (
      this.f.clientName === "" ||
      this.f.clientContact === "" ||
      this.f.clientEmail === "" ||
      this.f.clientSubject === "" ||
      this.f.clientContent === ""
    ) {
      //this.alerts.setMessage('All the fields are required','error');
      this.modelAlertContent = "All the fields are required";
      return false;
    }
    this.blockUI.start("");
    this.page.sendContact(this.f).then(
      res => {
        if (res["success"]) {
          this.blockUI.stop();
          this.modelAlertContent =
            "<span class='fc-primary'>Contact form has sent successfully.</span>";
          this.resetForm();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );
  }

  resetForm() {
    this.f = {
      clientName: "",
      clientContact: "",
      clientEmail: "",
      clientSubject: "",
      clientContent: ""
    };
  }
}
