import { Component, OnInit, ViewChild, Input, Output } from "@angular/core";
import { NgbModal, ModalDismissReasons, NgbDatepickerConfig, NgbCalendar, NgbDateStruct} from "@ng-bootstrap/ng-bootstrap";
import { environment } from "../../environments/environment";
import { FormsModule } from "@angular/forms";
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { PageService } from "../api/page.service";

@Component({
  selector: "app-quote-form",
  templateUrl: "./quote-form.component.html",
  styleUrls: ["./quote-form.component.css"]
})
export class QuoteFormComponent implements OnInit {
  @ViewChild("quoteContent")
  content: any;
  @ViewChild("returnDateRange")
  returnDateRange: any;
  @BlockUI()
  blockUI: NgBlockUI;
  quoteForm: any;
  selectSettings = {
    display: "center"
  };
  formSettings: any = {
    onInit: (e, ins) => {
      this.quoteForm = ins;
    }
  };

  modelAlertContent = "";
  rooms: any;
  apiUrl = "";
  title = "";
  hotelId = "";
  modalRef: any;
  currentImage = "assets/images/sunset.jpg";
  Object = Object;
  noOfRoom = 0;
  f = {
    adults: [],
    kids: [],
    room: [],
    roomTitle: [],
    meal: [],
    name: "",
    contact_no: "",
    email: "",
    nationality: "",
    offerType: "",
    offerDetail: "",
    arrivalDate: "",
    departureDate: "",
    hotel_name: ""
  };
  range: any;
  rangeSettings: any;

  constructor(private modalService: NgbModal, private page: PageService, config: NgbDatepickerConfig, calendar: NgbCalendar) {
    var today = new Date();
    var dd = today.getDate();
    var mm = today.getMonth()+1; //January is 0!
    var yyyy = today.getFullYear();

    // if(dd<10) {
    //     dd = '0'+dd
    // } 

    // if(mm<10) {
    //     mm = '0'+mm
    // } 

    // today = mm + '/' + dd + '/' + yyyy;
    // document.write(today);
    // customize default values of datepickers used by this component tree
    config.minDate = {year: yyyy, month: mm, day: dd};
    config.maxDate = {year: 2099, month: 12, day: 31};

    // days that don't belong to current month are not visible
    config.outsideDays = 'hidden';

  }

  show(hotelId, title, rooms, roomId) {
    this.apiUrl = environment.site_url;
    this.rooms = rooms;
    this.title = title;
    this.f.hotel_name = title;
    this.hotelId = hotelId;
    this.modalRef = this.modalService.open(this.content, {
      windowClass: "xlModal",
      size: "lg",
      backdrop: "static"
    });
    console.log(rooms);
    console.log(roomId);
    if (roomId !== 0) {
      this.noOfRoom = 1;
      this.f.adults[0] = 2;
      this.f.room[0] = roomId;
      this.f.kids[0] = "";
      this.f.meal[0] = "FB";
      this.rooms.forEach(r => {
        if (r.id == roomId) {
          this.f.roomTitle[0] = r.title;
        }
      });
    }
  }

  close(event: any) {
    event.stopPropagation();
    this.modalRef.close();
  }

  ngOnInit() {
    this.apiUrl = environment.site_url;
    this.rangeSettings = {
      display: "center",
      controls: ["calendar"],
      startInput: "#departureDateInp",
      endInput: "#arrivalDateInp",
      min: new Date(),
      dateFormat: "ddd, M yy"
    };
  }

  addNRemoveRooms(event) {
    if (typeof event !== "undefined") {
      const inst = event.inst;
      inst.hide();
    }
    const tmpF = Object.assign({}, this.f);
    this.f.adults = [];
    this.f.kids = [];
    this.f.room = [];
    this.f.meal = [];

    for (var i = 0; i < this.noOfRoom; i++) {
      this.f.adults.push(
        typeof tmpF.adults[i] !== "undefined" ? tmpF.adults[i] : 0
      );
      this.f.kids.push(typeof tmpF.kids[i] !== "undefined" ? tmpF.kids[i] : 0);
      this.f.room.push(typeof tmpF.room[i] !== "undefined" ? tmpF.room[i] : 0);
      this.f.meal.push(typeof tmpF.meal[i] !== "undefined" ? tmpF.meal[i] : "");
    }
  }

  onSend() {
    console.log(this.f.offerDetail);
    if (
      this.f.adults.length === 0 ||
      this.f.adults[0] === "" ||
      this.f.adults[0] === "0" ||
      this.f.nationality === "" ||
      this.f.email === "" ||
      this.f.name === "" ||
      this.f.contact_no === ""
    ) {
      //this.alerts.setMessage('All the fields are required','error');
      this.modelAlertContent = "All fields are required";
      return false;
    }
    this.blockUI.start("");
    this.page.sendQuote(this.f).then(
      res => {
        if (res["success"]) {
          this.blockUI.stop();
          this.modelAlertContent =
            "<span class='fc-primary'>Quote has sent successfully.</span>";
          this.resetForm();
          //this.alerts.setMessage('Quote has sent successfuly','success');
          //this.modalRef.close();
        }
      },
      reject => {
        console.log("error: ", reject);
        this.blockUI.stop();
      }
    );
  }

  roomChange(i) {
    this.f.roomTitle[i] = this.rooms[i]["title"];
  }

  resetForm() {
    this.noOfRoom = 0;
    this.f = {
      adults: [],
      kids: [],
      room: [],
      roomTitle: [],
      meal: [],
      name: "",
      contact_no: "",
      email: "",
      nationality: "",
      offerType: "",
      offerDetail: "",
      arrivalDate: "",
      departureDate: "",
      hotel_name: ""
    };
  }
}
