import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from "@angular/router";
import { trigger, state, style, animate, transition } from '@angular/animations';
import { Title } from "@angular/platform-browser";
import AOS from 'aos';
import { BlockUI, NgBlockUI } from "ng-block-ui";
import { PageService } from "../api/page.service";
import { environment } from "../../environments/environment";

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  animations: [
    trigger('fadeInOut', [
      state('void', style({
        opacity: 0
      })),
      transition('void <=> *', animate(1000)),
    ])
  ]
})
export class SearchComponent implements OnInit {
  // @BlockUI()
  // blockUI: NgBlockUI;  
  result = [];
  title = "Search DESTINATIONS";
  apiUrl = "";
  scrollDistance = 1;
  scrollUpDistance = 2;
  throttle = 0;
  perPage = 12;
  destinations = [];
  displayDestinations = [];
  contentText = "";
  totalPages = [];
  currentPage = 0;
  search_destination = "";

  constructor(
    private activatedRoute: ActivatedRoute,
    private page: PageService,
    private title_: Title,
    private router: Router
  ) {
    this.title_.setTitle("RAWNAQ Tourism | Search");
    activatedRoute.queryParams.subscribe(p => {
      this.search_destination = p.keyword ? p.keyword : '';
      this.loadDestinationsPage(1000, 0, 0);
    });
  }

  async ngOnInit() {
    AOS.init({
      once: true, disable: 'mobile'
    });
    // start the page from the top
    this.router.events.subscribe((evt) => {
      if (!(evt instanceof NavigationEnd)) {
        return;
      }
      window.scrollTo(0, 0)
    });
    this.apiUrl = environment.site_url;
    // this.blockUI.start("");
  }

  loadDestinationsPage(limit = 12, show_on_home = 0, page = 0) {
    var search_string = "";
    if (this.search_destination.trim() !== "") {
      search_string = this.search_destination.trim();
    }
    this.page.getAllDestination(limit, show_on_home, page, search_string).then(
      res => {
        console.log("All Destination: ", res);
        if (res["success"]) {
          this.destinations = res["response"];
          this.result = [];
          this.currentPage = res["current_page"];
          // console.log(res);
          this.totalPages = Array(res["total_pages"])
            .fill(0)
            .map((x, i) => i);
          const BreakException = {};
          try {
            this.destinations.forEach((s, i) => {
              if (this.perPage < i + 1) {
                throw BreakException;
              }
              this.result.push(this.destinations[i]);
            });
          } catch (e) {
            if (e !== BreakException) {
              throw e;
            }
          }
          // this.blockUI.stop();
        } else {
          this.result = [];
        }
      },
      reject => {
        console.log("error: ", reject);
        // this.blockUI.stop();
      }
    );
  }
}

