import { Injectable, Injector } from "@angular/core";
import { ApiService } from "../api.service";
import { Router } from "@angular/router";

import { HomeComponent } from "../home/home.component";
import { LoginComponent } from "../login/login.component";
import { PageComponent } from "../page/page.component";
import { DestinationsComponent } from "../destinations/destinations.component";
import { ExploreComponent } from "../explore/explore.component";
import { HotelComponent } from "../hotel/hotel.component";
import { ContactFormComponent } from "../contact-form/contact-form.component";
import { HowWeWorkComponent } from "../how-we-work/how-we-work.component";
import { AboutUsComponent } from "../about-us/about-us.component";
import { BankDetailsComponent } from "../bank-details/bank-details.component";
import { OnlineBookingComponent } from "../online-booking/online-booking.component";
import { SearchComponent } from "../search/search.component";

@Injectable()
export class RoutesService {
  currentSettings: any;
  constructor(private injector: Injector, private apiService: ApiService) { }

  getNavigation() {
    return this.apiService.get("default/get-all-navigation").toPromise();
  }

  createNavigation(): Promise<any> {
    return new Promise((resolve, reject) => {
      setTimeout(() => {
        const router = this.injector.get(Router);
        //console.log(router);
        this.apiService.get("default/get-all-navigation").subscribe(
          response => {
            router.config.push({
              path: "",
              component: HomeComponent,
              pathMatch: "full",
              data: { title: "Rawnaq" }
            });
            router.config.push({
              path: "login",
              component: LoginComponent,
              data: {
                path: "login",
                title: "Rawnaq - Login",
                label: "Rawnaq - Login",
                visible: "0"
              }
            });
            this.currentSettings = response;
            if (this.currentSettings && this.currentSettings.response) {
              this.currentSettings.response.push({
                label: "Search",    
                url: "search",
                visible: 0
              });
            }
            this.currentSettings.response.forEach(element => {
              //if(element.visible && element.visible === 1)
              let comp: any;
              if (element.url === "login") {
                comp = LoginComponent;
              } else if (
                typeof element.view != "undefined" &&
                element.view === "destinations"
              ) {
                comp = DestinationsComponent;
              } else if (
                typeof element.view != "undefined" &&
                element.view === "page"
              ) {
                comp = PageComponent;
              } else if (element.url.indexOf("explore/") > -1) {
                comp = ExploreComponent;
              } else if (element.url.indexOf("hotel/") > -1) {
                comp = HotelComponent;
              } else if (element.url.indexOf("how-we-work") > -1) {
                comp = HowWeWorkComponent;
              } else if (element.url.indexOf("about-us") > -1) {
                comp = AboutUsComponent;
              } else if (element.url.indexOf("online-booking") > -1) {
                comp = OnlineBookingComponent;
              } else if (element.url.indexOf("bank-details") > -1) {
                comp = BankDetailsComponent;
              } else if (element.url.indexOf("contact") > -1) {
                comp = ContactFormComponent;
              } else if (element.url.indexOf("search") > -1) {
                comp = SearchComponent;
              } else if (
                (element.url.indexOf("/") > -1 && element.url.length == 1) ||
                element.url.indexOf("index/") > -1 ||
                element.url.indexOf("home") > -1
              ) {
                comp = HomeComponent;
              } else {
                comp = PageComponent;
              }
              router.config.push({
                path: `${element.url}`,
                component: comp,
                data: {
                  path: element.url,
                  lable: element.label,
                  visible: element.visible,
                  view: element.view
                }
              });
              //router.config.push({ path: '/index', component: HomeComponent, pathMatch: 'full', data : {title: 'Rawnaq'} });
            });
            resolve(true);
          },
          err => {
            console.log(err);
            reject(false);
          }
        );
      });
    });
  }
}
